import { h, Component } from 'preact';
import { useEffect } from "preact/hooks";
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

function PageTitle(title, classes = '') {
	useEffect(() => {
		document.title = `Homerun Race: ${title}`;
	}, [title]);
	return <h1 class={classes}>{title}</h1>;
}

const initialState = {
};

@observer
class RaceHeader extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

    render() {
		const { raceStore, appState } = this.props.stores;
		const { title } = this.props;
		const { darkmode, language = 'en' } = appState;
		const { race } = raceStore;

		const today = new Date();
		let status = '';
		if (today >= new Date(race.startDate) && today <= new Date(race.endDate)) {
			status = 'race';
		} else if (today >= new Date(race.signupStartDate) && today <= new Date(race.signupEndDate)) {
			status = 'signup';
		} else if (today > new Date(race.endDate)) {
			status = 'done';
		}

		return (
			<>
				<div class='d-flex'>
					<div class='flex-fill'>
						{PageTitle(title, 'mb-0')}
					</div>
					<div class='flex-fill text-center d-none d-md-block'>
						<h1 class='mb-0' style='line-height: 0.8em;'><span class='font-weight-lighter' style='letter-spacing: -1px;'>{race.title}</span></h1>
						<small class=''>
							{race.startDate && <>{util.formatDate(race.startDate, { locale: language, hour12: false, hour: '2-digit', minute: '2-digit' })}</>}
							{race.endDate && <> - {util.formatDate(race.endDate, { locale: language, hour12: false, hour: '2-digit', minute: '2-digit' })}</>}
						</small>
					</div>
					<div class='flex-fill'>
						<h3 class='float-right'>
							{status === 'signup' && !race.liveRace && <>
								<div class='spinner-grow text-primary' role='status' />
								<span class='badge badge-primary ml-2'><Text id='races.signup-open'>Påmelding åpen</Text></span>
							</>}
							{status === 'race' && <>
								<div class='spinner-grow text-danger' role='status' />
								<span class='badge badge-danger ml-2'><Text id='races.race-in-progress'>Løp pågår</Text></span>
							</>}
							{status === 'done' && <>
								<i class='fas fa-check text-success' />
								<span class='badge badge-success ml-2'><Text id='races.race-finished'>Løp avsluttet</Text></span>
							</>}
						</h3>
					</div>
				</div>
				<div class='d-block d-md-none'>
					<h4 class='mb-0' style='line-height: 0.8em;'><span class='font-weight-light' style='letter-spacing: -1px;'>{race.title}</span></h4>
					<small>
						{race.startDate && <>{util.formatDate(race.startDate, { locale: language, hour12: false, hour: '2-digit', minute: '2-digit' })}</>}
						{race.endDate && <> - {util.formatDate(race.endDate, { locale: language, hour12: false, hour: '2-digit', minute: '2-digit' })}</>}
					</small>				
				</div>
			</>
		);
	}
}

export default RaceHeader;
