import { h, Component } from 'preact';
import { useEffect } from "preact/hooks";
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import Header from './header';
import Submenu from './submenu';
import RaceHeader from './raceheader';
import RaceText from './raceText';

import mu from '../../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true, true);
const MARGIN_BOTTOM = mu.marginBottom();

function asFacebookUser(input = '') {
	if (input.match(/facebook.com/)) {
		if (input.match(/^http/)) {
			return input;
		}
		return `https://${input}`;
	}
	return `https://facebook.com/${input}`;
}

function asInstagramUser(input) {
	if (input.match(/instagram.com/)) {
		if (input.match(/^http/)) {
			return input;
		}
		return `https://${input}`;
	}
	return `https://instagram.com/${input}`;
}

const initialState = {
	isLoading: false,
	email: '',
	submenu: 'ads',
	jwtToken: util.getJwtToken(),

	reloadInSec: null,
	reloadLastEpoch: null,
	reloadIsActive: false,
};

const RELOAD_INTERVAL_IN_SEC = 60;

@observer
class PublicRaceDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.updateTimer = {};
    }

	async loadRace(props = this.props) {
		this.setState({ isLoading: true });
		const { raceStore } = props.stores;
		const { raceid } = props;

		await raceStore.load(raceid);
		const { race } = raceStore;
		const allTeamIds = race.classes.map((currentClass) => {
			if (currentClass.contestants) {
				return currentClass.contestants.map(musher => musher.team)
			}
		});
		if (allTeamIds.length > 0) {
			await raceStore.loadTeams(allTeamIds.join(','));
			await raceStore.loadWorkoutSummary(allTeamIds.join(','), raceid);
		}
		// Load all images
		raceStore.getAllImages();

		const reloadLastEpoch = Math.floor(new Date().getTime() / 1000);
		this.setState({
			reloadIsActive: true,
			reloadLastEpoch,
		}, () => this.tickReloadTimeer());
		// Set reload timer
		clearTimeout(this.updateTimer.reload);
		this.updateTimer.reload = setTimeout(async () => {
			const reloadLastEpoch = Math.floor(new Date().getTime() / 1000);
			this.setState({ reloadLastEpoch });
			this.loadRace();
		}, RELOAD_INTERVAL_IN_SEC * 1000);

		this.setState({ isLoading: false });
	}

	tickReloadTimeer() {
		const { reloadIsActive, reloadLastEpoch } = this.state;
        if (reloadIsActive) {
			const now = Math.floor(new Date().getTime() / 1000);
			const reloadInSec = RELOAD_INTERVAL_IN_SEC - (now - reloadLastEpoch);
			this.setState({ reloadInSec });
			clearTimeout(this.updateTimer.tick);
			this.updateTimer.tick = setTimeout(() => this.tickReloadTimeer(), 1000);
        }
		return false;
    }

	copyText = (e) => {
		e.target.select();
	    document.execCommand('copy');
	}

	componentWillUnmount() {
		clearTimeout(this.updateTimer.reload);
		clearTimeout(this.updateTimer.tick);
	}

	componentDidMount() {
		this.loadRace();
	}

	componentWillReceiveProps(nextProps) {
		this.loadRace(nextProps);
	}

	render() {
		const { appState, raceStore, userStore } = this.props.stores;
		const { user = {}, isAdmin } = userStore;
		const { raceid, path } = this.props;
		const { darkmode, language = 'en' } = appState;
		const { race, teamList, images } = raceStore;

		const { reloadInSec } = this.state;
		const reloadPercent = Math.floor(util.normalizeBetween(reloadInSec, 0, RELOAD_INTERVAL_IN_SEC, 0, 100))

		return (
			<>
				<Header stores={this.props.stores} />

				<div class='container-fluid ' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
					<Submenu stores={this.props.stores} path={path} raceid={raceid} />

					<div class='row border-bottom '>
						<div class='col-12'>

							<Localizer>
								<RaceHeader stores={this.props.stores} title={<Text id='races.photos'>Bilder</Text>} />
							</Localizer>

							{reloadInSec >= 0 && <>
								<div class='progress fixed-top' style='height: 2px;'>
									<div class='progress-bar' role='progressbar' style={`width: ${reloadPercent}%;`} aria-valuenow={reloadPercent} aria-valuemin='0' aria-valuemax='100' />
								</div>
							</>}

							{user.email && <div class='alert alert-primary' role='alert'>
								{language === 'no' && <>
									Bildene hentes direkte fra The Musher appen.<br />
									<strong>For at bildene skal dukke opp må de:</strong>
									<ul>
										<li>Ligge på teamet som er påmeldt.</li>
										<li>Ligge på treningsøktene som er med i konkurransen.</li>
									</ul>
								</>}
								{language === 'en' && <>
									Bildene hentes direkte fra The Musher appen.<br />
									<strong>For at bildene skal dukke opp må de:</strong>
									<ul>
										<li>Ligge på teamet som er påmeldt.</li>
										<li>Ligge på treningsøktene som er med i konkurransen.</li>
									</ul>
								</>}
							</div>}

							<div class='p-3 my-4 bg-light'>
								<ul>
									{[1, 2, 3, 4].map(idx => <>
										{race[`mediaURL${idx}`] ? <li>
											<a href={race[`mediaURL${idx}`]} target='_blank' rel='noreferrer' native>
												{race[`mediaText${idx}`] ? race[`mediaText${idx}`] : <>Se bilder her</>} <i class='fas fa-chevron-circle-right' />
											</a>
										</li> : <></>}			
									</>)}
								</ul>
							</div>

							{(!images || images.length === 0) && <div class='d-flex justify-content-center my-5 py-5'>
								<div class='spinner-border text-primary' role='status'>
									<span class='sr-only'>Loading...</span>
								</div>
							</div>}

							<div class='row'>
								{images && images.map((image) => {
									const imageLink = image.s3LargeLink || image.s3MediumLink;
									if (!imageLink) {
										return (<></>);
									}

									const photoByline = image.title ? `${image.title}\n${image.body}\n` : `Photo by: ${image.musher.firstname || ''} ${image.musher.lastname || ''} / ${image.currentTeam.name || ''} / ${image.musher.sportsClub || ''}`;
									const tagLine = `${race.tag ? race.tag.split(',').map(tag => `#${tag}`).join(' ') : ''}`;
									const moreInfoFacebook = `All about the race: https://homerunrace.no/index#/race/${race.id}`;
									const moreInfoInstagram = `All about the race: homerunrace.no`;
									const facebookText = `${race.title}

${photoByline}

${tagLine}

${race.tagFacebookUsers ? race.tagFacebookUsers.split(',').map(usr => `@${usr}`).join(' ') : ''} ${image.musher.facebook ? `@${image.musher.facebook}` : ''}

${moreInfoFacebook}

`;
									const instagramText = `${race.title}

${photoByline}

${tagLine}

${race.tagInstagramUsers ? race.tagInstagramUsers.split(',').map(usr => `@${usr}`).join(' ') : ''} ${image.musher.instagram ? `@${image.musher.instagram}` : ''}

${moreInfoInstagram}

`;
									return (<>
										<div class='col-12 col-sm-6 col-md-4 col-lg-3 mb-4'>
											<div
												class={`text-center border rounded-lg`}
											>
												<img src={imageLink} class='img-fluid' />
											</div>
											<small class='text-muted float-right'>{util.formatDate(image.createdDate, { locale: language, hour12: false, hour: '2-digit', minute: '2-digit' })}</small>
											{image.title}
											{image.musher.firstname} {image.musher.lastname}<br />
											<span class='text-muted'>
												{image.currentTeam.name}
												{image.body}
											</span><br />
											{image.musher.sportsClub && <><small class='text-muted'>{image.musher.sportsClub}</small><br /></>}

											{isAdmin && <div class='p-2 bg-light border rounded-lg'>
												<small>
													Admin copy-paste:<br />
													<small class='text-muted float-right'><a href={image.s3XXLargeLink} rel='noreferrer' native target='_blank'>Download</a></small>
													<small class='text-muted'>Facebook:</small><br />
													<textarea style='width: 100%; font-size: 0.8em;' value={facebookText} onClick={this.copyText} />
													<small class='text-muted float-right'><a href={image.s3XXLargeLink} rel='noreferrer' native target='_blank'>Download</a></small>
													<small class='text-muted'>Instagram:</small><br />
													<textarea style='width: 100%; font-size: 0.8em;' value={instagramText} onClick={this.copyText} />
												</small>
											</div>}
										</div>
									</>);
								})}
							</div>

						</div>
					</div>
				</div>
			</>
		);
	}
}

export default PublicRaceDetail;
