import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import { toJS } from 'mobx'

import Map from '../../components/map';

import mu from '../../lib/musher-util';

const initialState = {
	showAddComment: false,
};

@observer
class Heatmap extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
		this.inputRef = {};
    }

	async loadHeatmap() {
		this.setState({ isLoading: true });
		const { adminStore } = this.props.stores;
        const { raceid } = this.props;
        await adminStore.getPublicHeatmap(raceid);
		this.setState({ isLoading: false });
	}

	componentDidMount() {
		this.loadHeatmap();
	}

    // componentWillReceiveProps() {
	// 	this.loadHeatmap();
	// }

    render() {
        const { adminStore } = this.props.stores;
        const { waypoints, zoomToMarkers, mapid, fullscreenMap } = this.props;
        const { publicHeatmap: heatmap } = adminStore;
        // const heatmap = publicHeatmap.slice(0, 10);
        // const heatmap = [[60.4433,8.8996,14],[61.384,9.1152,14],[60.42,8.9422,15]];

        return (
            <>
                {/* heatmap: {`map-${mapid}`}<br /> */}
                {/* {JSON.stringify(heatmap)} */}
                <Map
                    stores={this.props.stores}
                    mapId={heatmap.length}
                    heatmap={heatmap}
                    minZoom={2}
                    maxZoom={18}
                    height={600}
                    waypoints={waypoints}
                    zoomToMarkers={zoomToMarkers}
                    fullscreenMap={fullscreenMap}
                    key={`map-${mapid}`}
                />
            </>
        );
    }
}

export default Heatmap;
